var $grid;
const Works = (props) => {
    const [selectedItem, setSelectedItem] = useState("all");
    useEffect(() => {
        $grid = $(".works-submain").isotope({
            itemSelector: ".all",
            percentPosition: true,
            masonry: {
                columnWidth: ".all"
            }
        })
    })
    const handleCategoryClick = (category) => {
        setSelectedItem(category);
        $grid.isotope({
            filter: `.${category}`
        })
    }

    return (
        <div className="works-main">
            <div className="works-main-title">My <strong>Works</strong></div>
            <div className="works-categories">
                <div className={selectedItem === 'all' ? "works-category works-category-selected" : "works-category"} onClick={() => { handleCategoryClick('all') }}>all</div>
                <div className={selectedItem === 'works' ? "works-category works-category-selected" : "works-category"} onClick={() => { handleCategoryClick('works') }}>works</div>
                <div className={selectedItem === 'projects' ? "works-category works-category-selected" : "works-category"} onClick={() => { handleCategoryClick('projects') }}>projects</div>
            </div>
            <div className="works-submain">
                {worksAndProjects.map(item => {
                    const className = `works-container ${item.category} all`;
                    if (item.link) {
                        return (
                            <div className={className}>
                                <h3 className="works-title"><a href={item.link}>{item.title}</a></h3>
                                <p className="works-description" dangerouslySetInnerHTML={{__html: item.description}} />
                                {/* <p >{item.description}</p> */}
                            </div>
                        )
                    } else {
                        return (
                            <div className={className}>
                                <h3 className="works-title">{item.title}</h3>
                                <p className="works-description">{item.description}</p>
                            </div>
                        )
                    }
                })}
            </div>
        </div >
    )
}

export default Works;