import React from 'react';
import './Publications.css';

const publications = [
    {
        title: "Towards Automatic Prostate Gleason Grading via Deep Convolutional Neural Networks",
        description: "A. Khani, S.A. Fatemi, H. Otroshi Shahreza, H. Behroozi, M. Soleymani Baghshah, ”Towards Automatic Prostate Gleason Grading via Deep Convolutional Neural Networks”, The 5th Iranian Conference on Signal Processing and Intelligent Systems (ICSPIS 2019), Shahrood, Iran, 2019",
        link: "https://ieeexplore.ieee.org/document/9066019",
    },
    {
        title: "A Deep Learning Framework for Viable Tumor Burden Estimation",
        description: 'S.A. Fatemi, A. Khani, H. Otroshi Shahreza, H. Behroozi, M. Soleymani Baghshah, "A Deep \
        Learning Framework for Viable Tumor Burden Estimation", 2020 6th Iranian Conference on \
        Signal Processing and Intelligent Systems (ICSPIS), Mashhad, Iran, 2020',
        link: "https://ieeexplore.ieee.org/document/9349569",
    },
    {
        title: "MoNuSAC2020: A Multi-organ Nuclei Segmentation and Classification Challenge",
        description: 'Verma, Ruchika, Neeraj Kumar, Abhijeet Patil, Nikhil Cherian Kurian, Swapnil Rane, Simon Graham, Quoc Dang Vu et al. "MoNuSAC2020: A Multi-organ Nuclei Segmentation and Classification Challenge." IEEE Transactions on Medical Imaging (2021).',
        link: "https://ieeexplore.ieee.org/abstract/document/9446924",
    },
    {
        title: "A Unified Framework for Multi-organ Nuclei Segmentation and Classification Using Deep Convolutional Neural Networks.",
        description: 'Team, Sharif HooshPardaz, Seyed Alireza Fatemi Jahromi, Ali Asghar Khani, Ehsan Montahaei, Mahdieh Soleymani Baghshah, and Hamid Behroozi. "A Unified Framework for Multi-organ Nuclei Segmentation and Classification Using Deep Convolutional Neural Networks." Training 512: 512.',
        link: 'https://www.researchgate.net/publication/352227488_L5_and_PL3_A_Unified_Framework_for_Multi-organ_Nuclei_Segmentation_and_Classification_Using_Deep_Convolutional_Neural_Networks'
    },
]

const Publications = (props) => {
    return (
        <div className="publications-main">
            <div className="publications-main-title">My <strong>Publications</strong></div>
            {publications.map(publication => {
                return (
                    <div className="publications-container">
                        <h3 className="publications-title"><a href={publication.link}>{publication.title}</a></h3>
                        <div className="publications-description">{publication.description}</div>
                    </div>
                )
            })}
        </div>
    )
}

export default Publications;