import './Honors.css'
import CalendarTodayIcon from '@material-ui/icons/CalendarToday';

const honors = [
    {
        title: "Iran’s National Elites Foundation membership and grant",
        description: "I have been a recipient of Iran’s National Elites Foundation grant and membership because of outstanding academic success.",
        date: "Summer 2016 - Present",
    },
    {
        title: "5th rank of the PAIP2020 Challenge",
        description: "In this challenge, participants had to segment tumorous tissue and classify the MSI-High. We \
        participated in this challenge as Sharif HooshPardaz Team, and our team was made of my \
        friend \
        and me. In the end, we were invited to present our method in the PAIP2020, AI Pathology \
        Challenge Workshop.",
        date: "Summer 2020",
    },
    {
        title: "Participant in Iran's National Elites Foundation Research Project",
        description: 'I was a participant in the third and fourth rounds of a research project funded by \
        Iran’s National Elites Foundation and supervised by Prof. Behroozi and Prof. Soleymani.',
        date: "Spring 2019 - Summer 2020",
    },
    {
        title: "5th rank of the MoNuSAC 2020 Challenge",
        description: "Challenge's task was segmenting all cells in a WSI(Whole Slide Image) and then specifying all \
        cells' type. This challenge was an official satellite event of ISBI 2020. We participated in \
        this challenge in three people, and our group's name was Sharif HooshPardaz. Eventually, we \
        were \
        invited to present our method in the MoNuSAC 2020 Workshop.",
        date: 'Spring 2020'
    },
    {
        title: "3rd rank of the Eleventh Sharif VC Cup",
        description: "This challenge was about startups, and we participated in this challenge as the Sharif \
        HooshPardaz team.",
        date: "Winter 2020"
    },
    {
        title: "Reach the final stage of the Artificial Intelligence Challenge in Medical Imaging 2019, \
        Intelligent Assessment of Imaging Biomarkers of Dementia",
        description: "The task of this challenge was to find an anomaly in the brain MRI images.",
        date: "Fall 2019"
    },
    {
        title: "8th rank of the MICCAI Automatic Prostate Gleason Grading Challenge 2019",
        description: "In this challenge, we should first segment and then detect the Gleason grade in prostate \
        tissue. \
        We participated in this challenge as the Sharif HooshPardaz team in a group of two people. \
        Finally, we were invited to present our method at the MICCAI 2019 conference held in October \
        2019, in Shenzhen, China.",
        date: "Fall 2019"
    },
    {
        title: "120th rank of Konkur",
        description: "Konkur is Iran's national university entrance exam, and I ranked 120th in Math. and Phys. \
        field \
        among 163,000 participants.",
        date: "Summer 2016"
    },
]


const Honors = (props) => {    
    const first_column_number = Math.ceil(honors.length / 2);
    return (
        <div className="honors-main">
            <div className="honors-main-title">My <strong>Honors</strong></div>
            <div className="honors-submain">
                <div className="honors-column">
                    {honors.slice(0, first_column_number).map(honor => {
                        return (
                            <div className="honors-container">
                                <span className="honors-date"><CalendarTodayIcon style={{ fontSize: "0.8rem" }} />{honor.date}</span>
                                <h3 className="honors-title">{honor.title}</h3>
                                <p className="honors-description">{honor.description}</p>
                            </div>
                        )
                    })}
                </div>
                <div className="honors-column">
                    {honors.slice(first_column_number).map(honor => {
                        return (
                            <div className="honors-container">
                                <span className="honors-date"><CalendarTodayIcon style={{ fontSize: "0.8rem" }} />{honor.date}</span>
                                <h3 className="honors-title">{honor.title}</h3>
                                <p className="honors-description">{honor.description}</p>
                            </div>
                        )
                    })}
                </div>
            </div>
        </div>
    )
}

export default Honors;