import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';
import './NavigationBar.css'

const NavigationBar = (props) => {
  console.log(window.location.pathname)
  const history = useHistory();
  const [selectedItem, setSelectedItem] = useState(window.location.pathname);  
  const handleNavClick = (path) => {
    setSelectedItem(path)    
    history.push(path)
  }
  return (

    <div className={window.location.pathname == '/' ? "navbar-list" : "navbar-list-white"}>
      <div className={selectedItem == '/' ? "navbar-item navbar-selected" : "navbar-item"} onClick={() => {handleNavClick('/')}}>Home</div>
      <div className={selectedItem == '/publications' ? "navbar-item navbar-selected" : "navbar-item"} onClick={() => {handleNavClick('/publications')}}>Publications</div>
      <div className={selectedItem == '/honors' ? "navbar-item navbar-selected" : "navbar-item"} onClick={() => {handleNavClick('/honors')}}>Honors</div>
      <div className={selectedItem == '/works' ? "navbar-item navbar-selected" : "navbar-item"} onClick={() => {handleNavClick('/works')}}>Works</div>
    </div>


  )
}

export default NavigationBar;